(function () {
  let makeRemarkableSection = document.querySelector(
    '.section-make-remarkable'
  );

  if (makeRemarkableSection) {
    splitText('.section-make-remarkable .block-text *');
    splitText('.section-make-remarkable .block-link *');

    ScrollTrigger.saveStyles('.section-make-remarkable *');

    let tl = gsap.timeline({
      defaults: timelineDefaults,
      scrollTrigger: {
        trigger: makeRemarkableSection,
        start: 'top bottom-=30%',
      },
    });

    tl.fromTo(
      ['.block-text span, .block-link span'],
      { visibility: 'hidden' },
      { visibility: 'visible', stagger: 0.02, delay: 0.1 }
    );
  }
})();
