(function () {
  let scrollPosition = 0;

  // Lock Scroll
  function lockScroll(target) {
    scrollPosition = window.pageYOffset;
    target.style.overflow = 'hidden';
    target.style.position = 'fixed';
    target.style.top = `-${scrollPosition}px`;
    target.style.width = '100%';
  }

  // Unlock Scroll
  function unlockScroll(target) {
    target.style.removeProperty('overflow');
    target.style.removeProperty('position');
    target.style.removeProperty('top');
    target.style.removeProperty('width');
    window.scrollTo(0, scrollPosition);
  }

  const burger = document.querySelector('.header-burger');
  const nav = document.querySelector('.nav');
  const navOverlay = nav.querySelector('.nav__overlay');
  const header = document.querySelector('.header');

  function showMenu() {
    nav.classList.add('active');
    header.classList.add('active');
    lockScroll(document.body);
  }

  function hideMenu() {
    nav.classList.remove('active');
    header.classList.remove('active');
    unlockScroll(document.body);
  }

  burger.addEventListener('click', function () {
    if (nav.classList.contains('active')) {
      hideMenu();
    } else {
      showMenu();
    }
  });

  navOverlay.addEventListener('click', hideMenu);
})();
