(function () {
  let faqSection = document.querySelector('.section-faq');

  if (faqSection) {
    (function () {
      const showMoreBtn = faqSection.querySelector('.faq__button span');
      showMoreBtn.addEventListener('click', expandFaqs);

      function expandFaqs() {
        const faqs = faqSection.querySelectorAll('.faq .faq-item');

        faqs.forEach((faq) => {
          faq.style.display = 'grid';
        });

        showMoreBtn.style.display = 'none';
      }
    })();
  }
})();
