(function () {
  let structuringSections = document.querySelectorAll('.section-structuring');

  structuringSections.forEach((section) => {
    if (section) {
      let sectionAllElements = section.querySelectorAll('*');
      let sectionTitle = section.querySelector('.section-title');
      let blockTexts = section.querySelectorAll('.block-text p');
      let blockImg = section.querySelector('.block-img img');
      let blockCards = section.querySelectorAll('.block-cards__cards .card');
      let blockCardsNotice = section.querySelectorAll('.block-cards__notice');

      ScrollTrigger.saveStyles(sectionAllElements);

      let tl = gsap.timeline({
        defaults: timelineDefaults,
        scrollTrigger: {
          trigger: section,
          start: 'top bottom-=30%',
        },
      });

      tl.from(sectionTitle, { x: '-100%' });
      tl.from(blockTexts, {
        y: 10,
        autoAlpha: 0,
        stagger: 0.2,
      });

      if (blockImg) {
        tl.from(
          blockImg,
          {
            width: 0,
            duration: 0.7,
          },
          '-=.3'
        );

        tl.from(
          blockImg,
          {
            scaleX: 1.15,
            scaleY: 1.15,
            duration: 0.7,
          },
          '-=.3'
        );
      }

      if (blockCards) {
        tl.from(blockCards, { y: 20, autoAlpha: 0, stagger: 0.1 }, '-=.4');
      }

      if (blockCardsNotice) {
        tl.from(blockCardsNotice, fadeUp(), '-=.3');
      }
    }
  });
})();
