(function () {
  let textOnCenterSections = document.querySelectorAll(
    '.section-text-on-center'
  );

  textOnCenterSections.forEach((section) => {
    let sectionAllElements = section.querySelectorAll('*');
    ScrollTrigger.saveStyles(sectionAllElements);

    if (section.classList.contains('section-text-on-center--bg')) {
      let sectionBg = section.querySelector('.block-bg');
      let sectionText = section.querySelector('.block p');

      sectionBg.style.backgroundPosition = `50% ${innerHeight / 4}px`;

      gsap.to(sectionBg, {
        backgroundPosition: `50% ${-innerHeight / 4}`,
        ease: 'none',
        scrollTrigger: {
          trigger: section,
          scrub: true,
        },
      });

      gsap.from(sectionText, {
        y: 20,
        autoAlpha: 0,
        ease: 'none',
        scrollTrigger: {
          trigger: section,
          start: 'top bottom-=30%',
        },
      });
    }

    if (section && !section.classList.contains('section-text-on-center--bg')) {
      let sectionText = section.querySelectorAll('.block p span');

      let tl = gsap.timeline({
        defaults: timelineDefaults,
        scrollTrigger: {
          trigger: section,
          start: 'top bottom-=30%',
        },
      });

      tl.from(sectionText, { y: 20, autoAlpha: 0, stagger: 0.1, delay: 0.1 });
    }
  });
})();
