(function () {
  let getInTouchSection = document.querySelector('.section-get-in-touch');

  if (getInTouchSection) {
    const formFields = getInTouchSection.querySelectorAll('.form-field');

    formFields.forEach((field) => {
      field.addEventListener('click', () => {
        [].forEach.call(formFields, function (el) {
          el.classList.remove('is-focus');
        });

        field.classList.add('is-focus');
      });
    });

    ScrollTrigger.saveStyles('.section-get-in-touch *');

    let tl = gsap.timeline({
      defaults: timelineDefaults,
      scrollTrigger: {
        trigger: '.section-get-in-touch',
        start: 'top bottom-=30%',
      },
    });

    tl.from('.section-get-in-touch .section-title', fadeUp());

    tl.from(
      '.section-get-in-touch .section-heading__line',
      { x: '-105%' },
      '-=.3'
    );

    tl.from(
      '.section-get-in-touch .form-field .form-field__line',
      { x: '-105%', duration: 0.7, stagger: 0.1 },
      '-=.3'
    );

    tl.from(
      '.section-get-in-touch .form-field label',
      { autoAlpha: 0, stagger: 0.1 },
      '-=.5'
    );

    tl.fromTo(
      [
        '.section-get-in-touch .form-field input',
        '.section-get-in-touch .form-field textarea',
      ],
      { yPercent: 105 },
      { yPercent: 0, stagger: 0.1 },
      '-=.5'
    );

    tl.from('.section-get-in-touch .form-checkbox', { autoAlpha: 0 }, '-=.2');

    tl.from(
      '.section-get-in-touch .form-submit button',
      { yPercent: 105 },
      '-=.3'
    );
  }
})();
