(function () {
  const faqSection = document.querySelector('.section-faq');
  const nav = document.querySelector('.nav');
  const textPage = document.querySelector('.main_text-page');

  if (faqSection) {
    new Accordion('.accordion-container', {
      onOpen: function (currentElement) {
        currentElement.scrollIntoView({
          behavior: 'smooth',
          block: 'center',
        });
      },
    });
  }

  if (nav) {
    let navAcc = new Accordion('.nav-accordion', {
      duration: 400,
      elementClass: 'nav-ac',
      triggerClass: 'nav-ac-trigger',
      panelClass: 'nav-ac-panel',
    });
  }

  if (textPage) {
    let sidebarAcc = new Accordion('.sidebar-accordion', {
      elementClass: 'sidebar-ac',
      triggerClass: 'sidebar-ac-trigger',
      panelClass: 'sidebar-ac-panel',
    });

    // let index = null;
    const accLink = document.querySelectorAll(
      '.sidebar__item--accordion .sidebar__anchor'
    );
    [...accLink].map((link, idx) => {
      link.addEventListener('click', () => {
        // index = idx;
        sidebarAcc.toggle(idx);
      });
    });

    const links = document.querySelectorAll('.sidebar__anchor');
    [...links].map((link) => {
      link.addEventListener('click', () => {
        if (
          !link.parentElement.classList.contains('sidebar__item--accordion')
        ) {
          // sidebarAcc.close(index);
          sidebarAcc.closeAll();
        }
      });
    });

    let qaAcc = new Accordion('.qa', {
      elementClass: 'qa-block',
      triggerClass: 'qa-block__title',
      panelClass: 'qa-block__text',
      onOpen: function (currentElement) {
        const id = currentElement.id;
        const yOffset = document.querySelector('.header').offsetHeight + 30;
        const element = document.getElementById(id);
        const y =
          element.getBoundingClientRect().top + window.pageYOffset - yOffset;

        window.scrollTo({ top: y, behavior: 'smooth' });
      },
    });

    const accSubLink = document.querySelectorAll('.sidebar__sub-anchor');
    [...accSubLink].map((link, idx) => {
      link.addEventListener('click', () => {
        qaAcc.open(idx);
      });
    });
  }
})();
