(function () {
  let imgTextSections = document.querySelectorAll('.section-img-text');

  imgTextSections.forEach((section) => {
    if (section) {
      let sectionAllElements = section.querySelectorAll('*');
      let sectionTitle = section.querySelector('.section-title');
      let sectionImg = section.querySelector('.img-text__img img');
      let sectionText = section.querySelectorAll('.img-text__text p');

      ScrollTrigger.saveStyles(sectionAllElements);

      let tl = gsap.timeline({
        defaults: timelineDefaults,
        scrollTrigger: {
          trigger: section,
          start: 'top bottom-=30%',
        },
      });

      tl.to(sectionImg, {
        scaleX: 1,
        scaleY: 1,
        delay: 0.1,
      });
      tl.from(sectionTitle, { x: '-100%', duration: 0.8 }, '-=.5');
      tl.from(sectionText, { y: 10, autoAlpha: 0, stagger: 0.2 });
    }
  });
})();
