(function () {
  let offersSection = document.querySelector('.section-offers');

  if (offersSection) {
    let offers = offersSection.querySelector('.offers');
    let offerItems = offersSection.querySelectorAll('.offers__item');

    // if offer items less than 2
    offerItems.length <= 2 ? offers.classList.add('offers--fill') : '';

    (function () {
      const textLimit = 160;

      offerItems.forEach((offer) => {
        const offerText = offer.querySelector('.offer__text p');
        if (offerText.textContent.length > textLimit) {
          offerText.textContent = `${offerText.textContent.substring(
            0,
            textLimit
          )}`;
        }
      });
    })();

    (function () {
      ScrollTrigger.saveStyles('.section-offers *');

      let tl = gsap.timeline({
        defaults: timelineDefaults,
        scrollTrigger: {
          trigger: offersSection,
          start: 'top bottom-=30%',
        },
      });

      tl.from('.section-heading .section-title', fadeUp());
      tl.from('.section-heading p', fadeUp(), '-=.4');

      offerItems.forEach((offer) => {
        let offerIcon = offer.querySelector('.offer__img');
        let offerTitle = offer.querySelector('.offer__title');
        let offerText = offer.querySelector('.offer__text');

        let tl = gsap.timeline({
          defaults: timelineDefaults,
          scrollTrigger: {
            trigger: offer,
            start: `top bottom-=30%`,
          },
        });

        tl.to(offerIcon, { y: 0, autoAlpha: 1, delay: 0.2 });
        tl.to(offerTitle, { y: 0, autoAlpha: 1 }, '-=.4');
        tl.to(offerText, { y: 0, autoAlpha: 1 }, '-=.3');
      });
    })();
  }
})();
