(function () {
  let faqSection = document.querySelector('.section-faq');

  if (faqSection) {
    (function () {
      ScrollTrigger.saveStyles('.section-faq *');

      const faqItems = faqSection.querySelectorAll('.faq-item');

      faqItems.forEach((faq) => {
        const faqLineTop = faq.querySelector('.faq-item__line--top');
        const faqLineBottom = faq.querySelector('.faq-item__line--bottom');
        const faqNum = faq.querySelector('.faq-item__num');
        const faqQ = faq.querySelector('.faq-item__q');

        let tl = gsap.timeline({
          defaults: timelineDefaults,
          scrollTrigger: {
            trigger: faq,
            start: 'top bottom-=30%',
          },
        });

        tl.to(faqLineTop, { width: '100%' });
        tl.from(faqNum, fadeUp(), '-=.3');
        tl.from(faqQ, fadeUp(), '-=.2');
        tl.to(faqLineBottom, { width: '100%' }, '-=.3');
      });
    })();
  }
})();
