(function () {
  let bannerSection = document.querySelector('.section-banner');

  if (bannerSection) {
    ScrollTrigger.saveStyles('.section-banner *');

    let tl = gsap.timeline({
      defaults: timelineDefaults,
      scrollTrigger: {
        trigger: bannerSection,
        start: 'top bottom-=30%',
      },
    });

    tl.to('.section-banner .section-title', {
      autoAlpha: 1,
      x: 0,
      delay: 0.2,
    });
    tl.from('.section-banner .section-title span', { y: '120%' }, '-=.1');
    tl.from('.section-banner .breadcrumbs', fadeUp(), '-=.1');
  }
})();
